  font-size: 1.125rem;
  line-height: 1.75rem;
}

@media (min-width: 640px) {
  .main-nav {
    display: -webkit-flex;
    display: flex;
  }
}

.main-nav li {
  margin-top: 3rem;
  margin-bottom: 3rem;
  text-align: center;
}

@media (min-width: 640px) {
  .main-nav li {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 3rem;
  }
}

.main-nav li a {
  cursor: pointer;
}

.main-nav li a:hover {
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.main-nav li a.active {
  border-color: var(--main-brand);
  border-bottom-width: 2px;
  font-weight: 700;
  color: var(--main-brand);
}

.soc-icon {
  background-color: var(--card-bg);
}

.soc-icon:hover {
  background-color: var(--main-accent);
}

