  border-radius: 9999px;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 2.5rem;
  width: 2.5rem;
}

.tag-list {
  display: -webkit-flex;
  display: flex;
  font-size: 0.75rem;
  line-height: 1rem;
  margin-top: 1.5rem;
}

.tag {
  background-color: var(--card-button-bg);
  border-radius: 0.75rem;
  font-weight: 700;
  padding: 0.5rem;
  color: var(--main-bg);
}

.tag:hover {
  color: var(--main-brand);
}

.card {
  background-color: var(--card-bg);
  border-radius: 0.75rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: space-between;
          align-content: space-between;
  padding: 1rem;
