.card-title {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.75rem;
  margin-bottom: 1.5rem;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.card-subtitle {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--main-brand);
  width: 100%;
}

.video-container {
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0px;
  overflow: hidden;
  position: relative;
}

.video-container iframe, .video-container object, .video-container embed {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

